.Container {

    padding: 15px;
    
    margin-left: auto;
    margin-right: auto;
    outline: 1px solid red;
  }
@media screen and (min-width: 320px) {
    .Container {
      width: 320px;
      outline: 1px solid green;
    }
  }
  
  @media screen and (min-width: 760px) {
    .Container {
      width: 720px;
      outline: 1px solid blue;
    }
  }
  
  @media screen and (min-width: 1600px) {
    .Container {
      width: 1368px;
      outline: 1px solid red;
    }}
